import React, {Component} from 'react'
import * as CONSTANT from '../../../constant'
import { withStyles } from '@material-ui/core/styles'
import Drawer from '@material-ui/core/Drawer'
import Hidden from '@material-ui/core/Hidden'

import AppMenu from './AppMenu/AppMenu'

const styles = theme => ({
  root: {
    position: 'relative'
  },
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: CONSTANT.APP_BAR_WIDTH,
    [theme.breakpoints.up('md')]: {
      position: 'relative'
    }
  }
})

class AppBarContent extends Component {
  constructor () {
    super()
    this.state = {}
  }

  render () {
    const {isMobileOpen, classes, theme, toggleMobileDrawer} = this.props

    return (
      <div>
        <Hidden mdUp>
          <Drawer
            variant='temporary'
            anchor={theme.direction === 'rtl' ? 'right' : 'left'}
            open={isMobileOpen}
            onClose={toggleMobileDrawer}
            elevation={4}
            classes={{
              paper: classes.drawerPaper
            }}
            ModalProps={{
              keepMounted: true // Better open performance on mobile.
            }}>
            <AppMenu toggleMobileDrawer={toggleMobileDrawer} />
          </Drawer>
        </Hidden>
        <Hidden smDown implementation='css'>
          <Drawer
            variant='permanent'
            open
            elevation={4}
            classes={{
              paper: classes.drawerPaper
            }}>
            <div className={classes.toolbar} />
            <AppMenu />
          </Drawer>
        </Hidden>
      </div>
    )
  }
}

export default withStyles(styles, { withTheme: true })(AppBarContent)



