import React from 'react'
import Loadable from 'react-loadable'

import AppLoading from './container/AppLoading'

import InputIcon from '@material-ui/icons/Input'
import AccountCircleIcon from '@material-ui/icons/AccountCircle'

const lazyloadComponent = (componentName) => {
  return Loadable({
    loader: () => import(`./container/${componentName}`),
    loading: AppLoading
  })
}

const RouteConfig = [
  {
    page: 'Signin',
    path: '/signin',
    isVisiable: true,
    component: lazyloadComponent('Signin'),
    icon: <InputIcon />
  }, {
    page: 'Registers',
    path: '/registers',
    isVisiable: true,
    component: lazyloadComponent('Registers'),
    icon: <AccountCircleIcon />
  }, {
    page: 'User',
    path: '/user/:key',
    isVisiable: false,
    component: lazyloadComponent('User'),
    icon: null
  }
]

export default RouteConfig



