import * as actionTypes from './actionTypes'

const API_SERVICE = 'https://us-central1-the-f2e-service.cloudfunctions.net'
const FIREBASE_SERVICE = 'https://the-f2e-service.firebaseio.com'

export const getGithubInfo = () => {
  return (dispatch) => {
    fetch('https://api.github.com/users/sky172839465')
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.GET_USER_INFO,
          payload: {
            userInfo: data
          }
        })
      })
  }
}

export const getCityAreaList = () => {
  return (dispatch) => {
    fetch(`${FIREBASE_SERVICE}/validation/cityAreaList.json`)
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.GET_CITY_AREA_LIST,
          payload: {
            cityAreaList: data
          }
        })
      })
  }
}

export const getRegisterUser = (key) => {
  return (dispatch) => {
    fetch(`${FIREBASE_SERVICE}/validation/registerList.json?orderBy="$key"&equalTo="${key}"`)
      .then(res => res.json())
      .then(data => {
        return new Promise((resolve) => setTimeout(() => resolve(data), 1000))
      })
      .then(data => {
        const isUserExist = Object.keys(data).includes(key)
        const registerUser = isUserExist ? data[key] : {}
        dispatch({
          type: actionTypes.GET_REGISTER_USER,
          payload: {
            registerUser
          }
        })
      })
  }
}

export const getRegisterList = () => {
  return (dispatch) => {
    fetch(`${API_SERVICE}/getRegisterList`)
      .then(res => res.json())
      .then(data => {
        return new Promise((resolve) => setTimeout(() => resolve(data), 1000))
      })
      .then(data => {
        dispatch({
          type: actionTypes.GET_REGISTER_LIST,
          payload: {
            registerList: data.result
          }
        })
      })
  }
}

export const addNewRegister = (register) => {
  return (dispatch) => {
    fetch(`${API_SERVICE}/addNewRegister`, {
      method: 'post',
      body: JSON.stringify({...register})
    })
      .then(res => res.json())
      .then(data => {
        return new Promise((resolve) => setTimeout(() => resolve(data), 3000))
      })
      .then(data => {
        dispatch({
          type: actionTypes.ADD_NEW_REGISTER,
          payload: {
            registerList: data.result
          }
        })
      })
  }
}

export const deleteRegisterItem = (register) => {
  return (dispatch) => {
    const {key, filePath} = register
    fetch(`${API_SERVICE}/deleteRegisterItem`, {
      method: 'post',
      body: JSON.stringify({
        key,
        file: {...filePath}
      })
    })
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.DELETE_REGISTER_ITEM,
          payload: {
            registerList: data.result
          }
        })
      })
  }
}



