import React, {Component} from 'react'
import {Link, withRouter} from 'react-router-dom'
import { withStyles } from '@material-ui/core/styles'
import RouteConfig from '../../../../RouteConfig'

import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'

const styles = theme => ({
  root: {
    paddingTop: 0
  },
  active: {
    backgroundColor: '#ccc'
  }
})

class AppMenu extends Component {
  constructor (props) {
    super(props)
    this.state = {
      currentRoute: this.props.location.pathname
    }
  }

  componentWillUnmount () {
    this.unlisten()
  }

  componentDidMount () {
    const {history} = this.props

    this.unlisten = history.listen(location => {
      this.setState({currentRoute: location.pathname})
    })
  }

  render () {
    const {classes, toggleMobileDrawer} = this.props
    const {currentRoute} = this.state

    return (
      <List component='nav' className={classes.root}>
        {RouteConfig.filter(route => route.isVisiable).map(route => {
          return (
            <ListItem
              button
              divider
              key={route.page}
              component={Link}
              to={route.path}
              classes={currentRoute === route.path ? {root: classes.active} : {}}
              onClick={toggleMobileDrawer}>
              <ListItemIcon>{route.icon}</ListItemIcon>
              <ListItemText inset primary={route.page} />
            </ListItem>
          )
        })}
      </List>
    )
  }
}

export default withStyles(styles)(withRouter(AppMenu))



