import React, {Component} from 'react'
import {Link} from 'react-router-dom'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import withWidth from '@material-ui/core/withWidth'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import Hidden from '@material-ui/core/Hidden'

import MenuIcon from '@material-ui/icons/Menu'
import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore'

import deepPurple from '@material-ui/core/colors/deepPurple'
import purple from '@material-ui/core/colors/purple'

const styles = theme => ({
  root: {
    zIndex: theme.zIndex.drawer + 1,
    background: `linear-gradient(to right, ${deepPurple[500]}, ${purple[100]}, ${deepPurple[500]})`
  },
  header: {
    background: `linear-gradient(to right, rgba(0, 0, 0, .7), rgba(0, 0, 0, .3), rgba(0, 0, 0, .7))`
  },
  viewOnGithub: {
    position: 'fixed',
    top: 0,
    right: 0,
    height: 64,
    zIndex: 1,
    [theme.breakpoints.only('xs')]: {
      height: 56
    }
  }
})

class AppHeaderContent extends Component {
  render () {
    const { classes, isMobileOpen, toggleMobileDrawer, width, currentRoute } = this.props

    return (
      <AppBar position='absolute' className={classes.root}>
        <Toolbar
          disableGutters={width === 'xs' && !isMobileOpen}
          className={classes.header}>
          <Typography variant='title' color='inherit' noWrap>
            {
              currentRoute === '/user'
                ? (
                  <IconButton
                    color='inherit'
                    aria-label='back to registers'
                    component={Link}
                    to='/registers'>
                    <NavigateBeforeIcon />
                  </IconButton>
                )
                : (
                  <Hidden mdUp={width !== 'xs'}>
                    <IconButton
                      color='inherit'
                      aria-label='open drawer'
                      onClick={toggleMobileDrawer}>
                      <MenuIcon />
                    </IconButton>
                  </Hidden>
                )
            }
            CH06 Validation
          </Typography>
          <a href='https://github.com/sky172839465/the-f2e-ch06-validation' target='block'>
            <img
              className={classes.viewOnGithub}
              src={`https://sky172839465.github.io/the-f2e/img/view-on-github.black.png?now=${Date.now()}`}
              alt='View on github' />
          </a>
        </Toolbar>
      </AppBar>
    )
  }
}

AppHeaderContent.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(withWidth()(AppHeaderContent))



