import React, { Component } from 'react'
import { connect } from 'react-redux'
import { HashRouter, Switch, Route, Redirect } from 'react-router-dom'
import { withStyles } from '@material-ui/core/styles'

import Grid from '@material-ui/core/Grid'

import RouteConfig from './RouteConfig'
import AppHeader from './container/Common/AppHeader'
import AppBar from './container/Common/AppBar'
import NoFoundPage from './component/404'

const styles = theme => ({
  root: {
    flexGrow: 1,
    height: '100vh',
    zIndex: 1,
    overflow: 'auto',
    position: 'relative',
    display: 'flex'
  },
  main: {
    maxWidth: 500
  },
  toolbar: theme.mixins.toolbar
})

class App extends Component {
  render () {
    const {classes, currentRoute} = this.props

    return (
      <HashRouter>
        <div className={classes.root}>
          <AppHeader />
          {currentRoute === '/user' ? null : <AppBar />}
          <main className={classes.root}>
            <Grid container spacing={0} justify='center'>
              <Grid item xs={12} sm={10} md={8} className={classes.main}>
                <div className={classes.toolbar} />
                <Switch>
                  <Route exact path='/' render={() => <Redirect to={RouteConfig[0].path} />} />
                  {RouteConfig.map(route => {
                    return <Route path={route.path} component={route.component} key={route.page} />
                  })}
                  <Route component={NoFoundPage} />
                </Switch>
              </Grid>
            </Grid>
          </main>
        </div>
      </HashRouter>
    )
  }
}

const mapStateToProps = state => {
  return {
    currentRoute: state.action.currentRoute
  }
}

const mapDispatchToProps = dispatch => {
  return {
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(App))



