import * as type from './actions/actionTypes'

const initialState = {
  isDataLoaded: true,
  isMobileOpen: false,
  isRegisterSuccess: false,
  userInfo: {},
  currentRoute: '',
  cityAreaList: [],
  registerList: [],
  registerUser: {}
}

const actionReducer = (state = initialState, action) => {
  switch (action.type) {
    case type.GET_USER_INFO:
      return {
        ...state,
        isDataLoaded: true,
        userInfo: {...action.payload.userInfo}
      }
    case type.GET_CITY_AREA_LIST:
      return {
        ...state,
        isDataLoaded: true,
        cityAreaList: [...action.payload.cityAreaList]
      }
    case type.SET_DATA_LOADED:
      return {
        ...state,
        isDataLoaded: action.payload.isDataLoaded
      }
    case type.SET_CURRENT_ROUTE:
      return {
        ...state,
        currentRoute: action.payload.currentRoute
      }
    case type.TOGGLE_MOBILE_DRAWER:
      return {
        ...state,
        isMobileOpen: !state.isMobileOpen
      }
    case type.GET_REGISTER_LIST:
    case type.ADD_NEW_REGISTER:
    case type.DELETE_REGISTER_ITEM:
      return {
        ...state,
        isDataLoaded: true,
        isRegisterSuccess: true,
        registerList: action.payload.registerList
      }
    case type.SET_REGISTER_SUCCESS:
      return {
        ...state,
        isRegisterSuccess: action.payload.isRegisterSuccess
      }
    case type.GET_REGISTER_USER:
      return {
        ...state,
        isDataLoaded: true,
        registerUser: action.payload.registerUser
      }
    default:
      return state
  }
}

export default actionReducer



