import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as actionCreators from '../../store/actions'

import AppHeaderContent from '../../component/Common/AppHeader/AppHeaderContent'

class AppHeader extends Component {
  constructor () {
    super()
    this.state = {}
  }

  render () {
    return (
      <main>
        <AppHeaderContent {...this.props} />
      </main>
    )
  }
}

const mapStateToProps = state => {
  return {
    isMobileOpen: state.action.isMobileOpen,
    currentRoute: state.action.currentRoute
  }
}

const mapDispatchToProps = dispatch => {
  return {
    toggleMobileDrawer: () => dispatch(actionCreators.toggleMobileDrawer())
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(AppHeader)



